
.footer {
  height: 540px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;

  &__logo {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    height: 76px;
  }

  &__copy {
  position: absolute;
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  bottom: 65px;
  left: 0;
  right: 0;
  }
}
